.caseCard{
    width: s(625);
    display: flex;
    flex-wrap: wrap;
    position: relative;

    &__link{
        @include cover();
        text-indent: -9999px;
        z-index: 2;
    }

    &__image{
        width: 100%;
        padding-top: s(500);
        height: 0;
        position: relative;
        margin-bottom: s(16);

        &:after{
            //@extend .photoNoise;
        }

        img{
            width: 100%;
            height: 100%;
            @include cover();
            object-fit: cover;
        }
    }

    &__videoContainer{
        width: 100%;
        padding-top: s(500);
        height: 0;

        position: relative;
        margin-bottom: s(16);

        &:after{
            //@extend .photoNoise;
        }
    }

    &__videoPoster{
        z-index: 1;
        opacity: 1;
        visibility: visible;
        transition: 200ms opacity easing(ease-out-quad), 0ms visibility linear 0ms ;
        .caseCard--playing &{
            opacity: 0;
            visibility: hidden;
            transition: 200ms opacity easing(ease-out-quad), 0ms visibility linear 200ms ;

        }
    }

    &__videoPoster,
    &__video{
        width: 100%;
        height: 100%;
        @include cover();
        object-fit: cover;
    }

    &__categories{
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-end;

        justify-self: flex-end;
        margin-left: auto;
    }

    &__categories,
    &__title{
        max-width: 50%;
    }
    

    &__title,
    &__category{
        @extend .body18;
    }

    &__category{
        opacity: 0.4;
        mix-blend-mode: plus-darker;
        display: flex;

        & + &{
            &:before{
                content: '/';
                margin-left: 6px;
                margin-right: 6px;

            }
        }
    }
}